// main-slider
(function() {

	$('.main-slider__slides').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: false,
		autoplaySpeed: 3000,
		dots: true,
		pauseOnDotsHover: true,
		infinite: true,
		pauseOnHover: true,
		arrows: true,
		useTransform: true,
		cssEase: 'ease',
		adaptiveHeight: true,
		responsive: [
			{
				breakpoint: 900,
				settings: {
					dots: false
				}
			},
			{
				breakpoint: 480,
				settings: {
					arrows: false,
					dots: false
				}
			}
		]
	});



	$('.main-slider__slides').on('afterChange', function (event, slick, currentSlide) {
		var $current = $(slick.$slides[currentSlide]);
		var $currentSlideVideo = $current.find('video');

		if ($current.hasClass('main-slider__item_video')) {
			$currentSlideVideo.get(0).play();
		}
	});

	$('.main-slider__slides').on('beforeChange', function (event, slick, currentSlide) {
		var $current = $(slick.$slides[currentSlide]);
		var $currentSlideVideo = $current.find('video');

		if ($current.hasClass('main-slider__item_video')) {
			$currentSlideVideo.get(0).pause();
		}
	});

})();
